@import url(https://fonts.googleapis.com/css?family=Muli:300,400,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.box,.container{display:flex;justify-content:center;align-items:center}html{background:#f1f3f6;font-family:"Muli",sans-serif;color:#333}#dribbble{position:fixed;display:block;right:70px;bottom:16px}#dribbble svg{display:block;width:76px;height:24px;fill:rgba(146,156,208,.8)}.App{text-align:center;padding:18px;margin:18px;border-radius:12px;box-shadow:inset 6px 6px 12px #b7b9bb,inset -6px -6px 12px #fff}.header{height:72px;padding:auto;margin:18px;border-radius:12px;background:#f1f3f6;box-shadow:inset 0 0 15px rgba(55,84,170,0),inset 0 0 20px rgba(255,255,255,0),7px 7px 15px rgba(55,84,170,.15),-7px -7px 20px #fff,inset 0px 0px 4px rgba(255,255,255,.2)}.container{width:100%;height:auto;padding:16px;margin:auto;border-radius:12px;background:#f1f3f6;display:flex;flex-wrap:wrap}.box{flex-direction:column;width:300px;height:420px;padding:auto;margin:42px;background:#f1f3f6;border-radius:20px;box-shadow:inset 0 0 15px rgba(55,84,170,0),inset 0 0 20px rgba(255,255,255,0),7px 7px 15px rgba(55,84,170,.15),-7px -7px 20px #fff,inset 0px 0px 4px rgba(255,255,255,.2)}.text{width:80%;height:auto}.text h1{font-size:1.5em;font-weight:600}.text p{margin-top:2em;line-height:1.45em}.btn{margin-top:2em;margin-bottom:2em;display:flex;flex-direction:column}.btn .cta{width:100px;height:33px;text-decoration:none;background:#2979ff;color:#fff;padding:10px 63px;border-radius:25px;font-weight:600;box-shadow:0px 10px 15px rgba(55,84,170,.25)}.tooltip{position:relative}.tooltip .tooltiptext{display:flex;justify-content:center;align-items:center;visibility:hidden;width:120px;background-color:#333;color:#fff;text-align:center;border-radius:25px;padding:5px 0 6px 0;position:absolute;top:-40px;left:calc(50% - 60px);z-index:1;font-size:.7em;opacity:0;box-sizing:border-box}.tooltip:hover .tooltiptext{visibility:visible;opacity:1;transition:opacity 399ms ease}
